/* card */
.lui-card {
  font-size: 14px;
  position: relative;
  overflow: hidden;
  margin: 10px;
  border-radius: 4px;
  background-color: #fff;
  background-clip: padding-box;
  box-shadow: 0px 0px 30px 0px rgba(51, 51, 51, 0.05);
}

.lui-card--shadow {
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.12);
}

.lui-card img {
  display: block;
  width: 100%;
}

.lui-card--hd {
  display: block;
  position: relative;
  padding: 20px;
}

.lui-card--hd_bb {
  padding: 18px 20px;
  border-bottom: 1px solid #ddd;
}

.lui-card--title {
  display: block;
  position: relative;
  color: #333;
  margin: 0;
  padding: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.2;
}

.lui-card--subtitle {
  display: block;
  position: relative;
  color: #999;
  margin: 0;
  padding: 0;
  font-size: 12px;
  letter-spacing: .4px;
  text-transform: uppercase;
}

.lui-card--title + .lui-card--subtitle,
.lui-card--subtitle + .lui-card--title {
  margin-top: 4px;
}

.lui-card--content {
  display: block;
  position: relative;
  font-size: 14px;
}

.lui-card--title + .lui-card--content,
.lui-card--subtitle + .lui-card--content {
  margin-top: 10px;
}

.lui-card--content_inner {
  position: relative;
  padding: 20px;
}

.lui-card--ft {
  position: relative;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  min-height: 44px;
  padding: 20px;
  -webkit-box-pack: justify;
  -webkit-justify-content: space-between;
  justify-content: space-between;
  -webkit-box-align: center;
  -webkit-align-items: center;
  align-items: center;
  font-size: 13px;
  color: #999;
}

.lui-card--ft:before {
  content: " ";
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  height: 1px;
  border-top: 1px solid #c7c7c7;
  color: #c7c7c7;
  transform-origin: 0 0;
  transform: scaleY(0.5);
}

/* media-box */
.lui-media-box {
  padding: 20px 20px;
  position: relative;
}

.lui-media-box:before {
  content: " ";
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  height: 1px;
  border-top: 1px solid #ddd;
  color: #ddd;
  transform-origin: 0 0;
  transform: scaleY(0.5);
  left: 20px;
}

.lui-media-box:first-child:before {
  display: none;
}

a.lui-media-box {
  color: #666;
  text-decoration: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

a.lui-media-box:active {
  background-color: #ddd;
}

.lui-media-box--title {
  font-weight: 400;
  font-size: 17px;
  width: auto;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  word-wrap: normal;
  word-wrap: break-word;
  word-break: break-all;
}

.lui-media-box--desc {
  font-size: 13px;
  line-height: 1.2;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
}

.lui-media-box--text .lui-media-box--title {
  margin-bottom: 8px;
}

.lui-media-box--graphic {
  display: flex;
  align-items: center;
}

.lui-media-box--graphic .lui-media-box--hd {
